import vCarousel from './components/carousel'
import vPage from './components/page'
export default {
   	data() {
      	return {
            navlist : [
               {
                  title : '',
                  subTle : 'ABOUT US',
                  link : '/about',
                  img : require('../assets/about.jpg')
               },
               {
                  title : '',
                  subTle : 'NEWS',
                  link : '/news',
                  img : require('../assets/news.jpg')
               },
               {
                  title : '',
                  subTle : 'CASES',
                  link : '/cases',
                  img : require('../assets/cases.jpg')
               },
               {
                  title : '',
                  subTle : 'SERVICE',
                  link : '/service',
                  img : require('../assets/server.jpg')
               },
               {
                  title : '',
                  subTle : 'TEAM',
                  link : '/team',
                  img : require('../assets/team.jpg')
               },
               {
                  title : '',
                  subTle : 'CONTACT US',
                  link : '/contact',
                  img : require('../assets/contact.jpg')
               }
            ],
            about_info : '2007LED',
            about_info2 : '',
            cases_desc : '',
            caseList : [
               {
                  link : '/',
                  src : require('../assets/caseCar1.jpg'),
                  title : ''
               },
               {
                  link : '/',
                  src : require('../assets/caseCar2.jpg'),
                  title : ''
               },
               {
                  link : '/',
                  src : require('../assets/caseCar3.jpg'),
                  title : ''
               },
               {
                  link : '/',
                  src : require('../assets/caseCar4.jpg'),
                  title : ''
               },
               {
                  link : '/',
                  src : require('../assets/caseCar5.jpg'),
                  title : '02'
               },
               {
                  link : '/',
                  src : require('../assets/caseCar6.jpg'),
                  title : ''
               },
            ],
            liWidth : 180,
            ulWidth : 0,
            ulLeft : 0,
            service_desc: ';',
            serviceList : [
               {
                  link : '/',
                  src : require('../assets/servicePic_1.jpg'),
                  title : ''
               },
               {
                  link : '/',
                  src : require('../assets/servicePic_2.jpg'),
                  title : ''
               },
               {
                  link : '/',
                  src : require('../assets/servicePic_1.jpg'),
                  title : ''
               },
               {
                  link : '/',
                  src : require('../assets/servicePic_2.jpg'),
                  title : ''
               },
            ],
            newsList : [
               {
                  link : '/news/msg_news',
                  title : '',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '**',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : ' ',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : ' ',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : 'TOM',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '2010',
                  date : '2017-12-21'
               },
            ],
            pages: [
               {
                  num: 1,
                  status: true
               },
               {
                  num: 2,
                  status: false
               },
            ],
            list: []
      	}
   	},
   	methods: {
         run (){
            this.timer = setInterval(()=>{
               let clientWidth = document.documentElement.clientWidth;
               if (clientWidth >= 640) {clientWidth = 640}
               if (this.ulWidth + this.ulLeft < clientWidth) {    
                  // ul+ululleft=0
                  this.ulLeft = 0;
               }else{
                  this.ulLeft-=2;
               }
            },30);
         },
         clear (){
            clearInterval(this.timer); // 
         }
   	},
      created() {
         this.ulWidth = (this.liWidth+16) * this.caseList.length; // uiWidth
         this.run();
         this.list = this.$store.getters.getCar                   // 
      },
      components: {
         vPage,vCarousel
      }
}



